<script>
	import { forceX, forceY, forceCollide, forceRadial } from 'd3-force';
	import { onMount } from 'svelte';
	import Force from './Force.svelte';

	let el;
	let width = 400;
	const height = 400;
	let centerPosition = [200, height / 2];
	let useForceCollide = true; // true = collision detection
	let useForceRadial = false; // true = donut

	$: activeForceX = forceX().x(centerPosition[0]);
	$: activeForceY = forceY().y(centerPosition[1]);
	$: activeForceCollide = forceCollide().radius(10).iterations(3);
	$: activeForceRadial = forceRadial()
		.radius(150)
		.x(centerPosition[0])
		.y(centerPosition[1]);
	$: forces = [
		['x', activeForceX],
		['y', activeForceY],
		useForceCollide && ['collide', activeForceCollide],
		useForceRadial && ['radial', activeForceRadial],
	].filter((d) => d);

	const numberOfDots = 200;
	let dots = new Array(numberOfDots).fill(0).map((_) => ({}));

	const onClick = (e) => {
		if (!el) return;
		const bounds = el.getBoundingClientRect();
		const x = e.clientX - bounds.left;
		const y = e.clientY - bounds.top;
		centerPosition = [x, y];
	};

	onMount(resize);
	function resize() {
		({ width } = el.getBoundingClientRect().width);
		centerPosition = [width / 2, height / 2];
	}
</script>

<svelte:window on:resize={resize} />

<div on:click={onClick} bind:this={el} bind:clientWidth={width}>
	<p>{width}px width</p>
	<Force {forces} {dots} {height} />
</div>

<style>
</style>
